import {Component} from 'react'
import './App.css'

class App extends Component{
	state ={
		advice: ''
	}
	componentDidMount(){
		this.fecthAdvice();

	}

	fecthAdvice = () =>{
		fetch('https://api.adviceslip.com/advice')
		.then(response => response.json())
		.then(data => {
			const {advice} = data.slip
			
			this.setState({advice})
		})
		
	}

	render(){
		const{advice} = this.state
		return(
			<div className ="app">
				<div className = 'text-container'>
					<h1>{advice}</h1>
					<a onClick={this.fecthAdvice}>Get Advice!</a>
				</div>
				<div className='mark'>Aliyu kabir 2021</div>
			</div>
			)
	}
}
export default App      